.form {
  @media (min-width: $desktop-width) {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}

.form__filters-wrapper {
  display: none;
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  z-index: 100000;
  background-color: $white;
  padding-top: 16px;
  padding-bottom: 16px;
  box-shadow: 0px 2.76726px 2px 0px rgba(0, 0, 0, 0.02), 0px 6.6501px 5.32008px 0px rgba(0, 0, 0, 0.03), 0px 12.52155px 10.01724px 0px rgba(0, 0, 0, 0.04);
 
  &--open {
    display: block;
  }

  @media (min-width: $desktop-width) {
    z-index: 100;
    display: flex;
    align-items: center;
    gap: 32px;
    position: static;
    padding: 0;
    box-shadow: none;
  }
}

.form__title {
  font-size: 24px;
  line-height: 32px;
  letter-spacing: -0.7px;
  margin-bottom: 16px;

  @media (min-width: $desktop-width) {
    display: none;
  }
}

.form__close-button {
  position: absolute;
  top: 20px;
  right: 16px;
  border: none;
  background-color: transparent;
  cursor: pointer;
  padding: 0;

  @media (min-width: $desktop-width) {
    display: none;
  }
}

.form__buttons-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-shrink: 0;
}

.form__button {
  border: none;
  background-color: transparent;
  padding: 0;
  font-family: "Manrope", sans-serif;
  font-size: 16px;
  font-weight: 500;
  line-height: 24px;
  letter-spacing: -0.5px;
  cursor: pointer;
  display: flex;
  flex-shrink: 0;
  align-items: center;
  gap: 8px;
  color: inherit;

  @media (min-width: $desktop-width) {
    font-size: 18px;
    line-height: 26px;

    &--open-filters {
      display: none;
    }
  }
}

.form__filter-list {
  display: flex;
  gap: 8px;
}

.form__filter-button {
  border-radius: 16px;
  padding: 4px 12px;
  border: 1px solid $border-grey;
  font-family: "Manrope", sans-serif;
  font-size: 12px;
  font-weight: 400;
  line-height: 16px;
  letter-spacing: -0.5px;

  @media (min-width: $desktop-width) {
    font-size: 14px;
    font-weight: 500;
    line-height: 20px;
  }
}