.warehouse-card {
  border-radius: 20px;
  background-color: $light-grey;
  padding: 16px;
  position: relative;

  @media (min-width: $desktop-width) {
    padding: 32px;
    display: grid;
    grid-template-columns: 179px auto 188px;
    gap: 20px;
  }
}

.warehouse-card__img {
  width: 71px;
  height: 48px;
  position: absolute;
  top: 16px;
  left: 16px;
  border-radius: 8px;

  @media (min-width: $desktop-width) {
    position: static;
    width: 179px;
    height: 102px;
    margin: 0 0 auto 0;
  }
}

.warehouse-card__container {
  &--contacts-mod {
    display: none;
  }

  @media (min-width: $desktop-width) {
    display: flex;
    flex-direction: column;

    &--contacts-mod {
      display: block;
    }
  }
}

.warehouse-card__title {
  font-size: 18px;
  font-weight: 700;
  line-height: 26px;
  letter-spacing: -0.5px;
  margin-left: 80px;
  min-height: 52px;

  @media (min-width: $desktop-width) {
    margin: 0;
    min-height: auto;
    font-size: 20px;
    line-height: 30px;
    width: fit-content;
  }
}

.warehouse-card__address {
  font-family: "Manrope", sans-serif;
  font-size: 14px;
  font-weight: 500;
  line-height: 20px;
  letter-spacing: -0.5px;
  display: block;
  margin-bottom: 16px;
  color: $grey;

  @media (min-width: $desktop-width) {
    margin: 0 0 0 24px;
    font-size: 16px;
    line-height: 24px;
  }
}

.warehouse-card__features-list {
  display: flex;
  flex-wrap: wrap;
  gap: 4px;
  margin-bottom: 16px;

  @media (min-width: $desktop-width) {
    margin: 0;
  }
}

.warehouse-card__features-item {
  font-family: "Manrope", sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 16px;
  letter-spacing: -0.5px;
  padding: 4px 12px;
  border-radius: 16px;
  border: 1px solid $border-grey;
  background-color: $white;

  @media (min-width: $desktop-width) {
    font-size: 14px;
    line-height: 20px;
  }
}

.warehouse-card__wrapper {
  @media (min-width: $desktop-width) {
    display: flex;
    align-items: baseline;
    margin-bottom: 12px;
  }
}

.warehouse-card__available {
  font-family: "Manrope", sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 20px;
  letter-spacing: -0.5px;
  margin-bottom: 4px;
  display: block;

  @media (min-width: $desktop-width) {
    font-size: 16px;
    line-height: 24px;
    margin: 0;
  }
}

.warehouse-card__size {
  display: inline-block;
  font-family: "Manrope", sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 20px;
  letter-spacing: -0.5px;
  margin: 0 8px 0 0;
    
  @media (min-width: $desktop-width) {
    margin: 0 8px 0 40px;
  }
}

.warehouse-card__space-list {
  font-family: "Manrope", sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 20px;
  letter-spacing: -0.5px;
  display: inline-flex;
  gap: 8px;
  margin-bottom: 16px;

  @media (min-width: $desktop-width) {
    margin: 0;
  }
}

.warehouse-card__price {
  display: block;
  font-size: 18px;
  font-weight: 700;
  line-height: 26px;
  letter-spacing: -0.5px;
  margin: 0 auto 8px auto;
  width: fit-content;

  @media (min-width: $desktop-width) {
    font-size: 20px;
    line-height: 30px;
    margin: auto auto 12px auto;
  }
}

.warehouse-card__link {
  padding: 8px 18px;
  color: $white;
  font-size: 16px;
  line-height: 24px;
  background-color: $background-dark-green;
  border-radius: 32px;
  display: block;
  text-align: center;

  &:hover {
    background-color: $green-hover;
  }

  @media (min-width: $desktop-width) {
    font-size: 18px;
    line-height: 26px;
    padding: 11px 20px;
  }
}
