.up-button {
  display: none;
  position: fixed;
  right: 16px;
  bottom: 16px;
  z-index: 10;
  width: 40px;
  height: 40px;
  cursor: pointer;
  border: none;
  border-radius: 16px;
  background-color: $light-grey;
  background-image: url("../images/svg/up-button-arrow.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;

  &:hover {
    background-color: $background-grey;
    box-shadow: 0 0 5px 2px rgba($black, 0.1);
  }

  &:active {
    background-color: $background-grey;
    box-shadow: 0 0 5px 1px rgba($black, 0.1);
  }

  @media (min-width: $desktop-width) {
    right: 64px;
    z-index: 10;
    width: 64px;
    height: 64px;
  }
}
