.period-property__title {
  font-size: 24px;
  line-height: 32px;
  letter-spacing: -0.7px;
  margin-bottom: 16px;

  @media (min-width: $desktop-width) {
    font-size: 48px;
    line-height: 56px;
    margin-bottom: 40px;
  }
}

.period-property__size-list {
  display: flex;
  flex-direction: column;
  gap: 8px;
  margin-bottom: 64px;

  @media (min-width: $desktop-width) {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
  }
}

.period-property__size-item {
  background-image: url("../images/svg/period-square-background.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  width: 288px;
  min-height: 181px;
  margin: 0 auto;
  display: flex;

  &:nth-child(2) {
    background-image: url("../images/svg/period-volume-background.svg");
  }

  &:nth-child(3) {
    background-image: url("../images/svg/period-amount-background.svg");
  }

  @media (min-width: $desktop-width) {
    width: 100%;
    min-height: 270px;
  }
}

.period-property__wrapper {
  margin: auto auto auto 32px;

  @media (min-width: $desktop-width) {
    margin: auto auto auto 56px;
  }
}

.period-property__size-title {
  font-size: 16px;
  line-height: 24px;
  letter-spacing: -0.5px;
  margin-bottom: 8px;

  @media (min-width: $desktop-width) {
    font-size: 18px;
    line-height: 26px;
  }
}

.period-property__size-value {
  font-size: 48px;
  line-height: 32px;
  letter-spacing: -1px;
}

.period-property__amount-list {
  padding: 0;
  margin: 0;
  list-style: none;
  width: 100px;

  @media (min-width: $desktop-width) {
    width: 110px;
  }
}

.period-property__amount-item {
  display: flex;
  justify-content: space-between;
  font-family: "Manrope", sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 20px;
  letter-spacing: -0.5px;

  @media (min-width: $desktop-width) {
    font-size: 18px;
    font-weight: 500;
    line-height: 26px;
  }
}

.period-property__amount-description {
  margin: 0;
}

.period-property__sup {
  font-size: 20px;
}

.period-property__img-list {
  display: flex;
  flex-direction: column;
  gap: 8px;
  margin-bottom: 64px;

  @media (min-width: $desktop-width) {
    flex-direction: row;
  }
}

.period-property__img-item {
  margin: 0 auto;
}


.period-property__img {
  width: 288px;
  height: 214px;

  @media (min-width: $desktop-width) {
    width: 100%;
    height: auto;
  }
}