.popup {
  display: none;
  width: 320px;
  margin: 0 auto;
  padding: 0 16px;
  background-color: $white;

  &--open {
    padding-top: 24px;
    display: block;
  }

  @media (min-width: $desktop-width) {
    padding: 32px;
    border-radius: 20px;
    position: absolute;
    top: 50%;
    left: 24px;
    transform: translateY(-50%);
    z-index: 1000;
    width: 408px;
  }
}

.popup__close-button {
  padding: 0;
  border: none;
  background-color: transparent;
  font-family: "Manrope", sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 20px;
  letter-spacing: -0.5px;
  display: flex;
  align-items: center;
  gap: 4px;
  margin-bottom: 4px;
  cursor: pointer;

  @media (min-width: $desktop-width) {
    margin-bottom: 24px;
  }
}

.popup__wrapper {
  position: relative;
  width: fit-content;
  margin: 0 auto 16px auto;

  @media (min-width: $desktop-width) {
    margin-bottom: 24px;
  }
}

.popup__img {
  border-radius: 16px;
}

.popup__container {
  @media (min-width: $desktop-width) {
    display: flex;
    justify-content: space-between;
    margin-bottom: 8px;
  }
}

.popup__available {
  position: absolute;
  top: 8px;
  right: 8px;
  font-family: "Manrope", sans-serif;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 20px;
  letter-spacing: -0.5px;
  padding: 4px 16px;
  background-color: rgba($white, 0.7);
  border-radius: 16px;
  backdrop-filter: blur(7px);
}

.popup__title {
  font-size: 18px;
  font-weight: 700;
  line-height: 26px;
  letter-spacing: -0.5px;
}

.popup__price {
  font-size: 18px;
  font-weight: 700;
  line-height: 26px;
  letter-spacing: -0.5px;
  display: block;
}

.popup__address {
  font-family: "Manrope", sans-serif;
  font-size: 14px;
  font-weight: 500;
  line-height: 20px;
  letter-spacing: -0.5px;
  display: block;
  margin-bottom: 16px;
  color: $grey;

  @media (min-width: $desktop-width) {
    margin-bottom: 8px;
  }
}

.popup__space-list {
  display: flex;
  flex-wrap: wrap;
  gap: 8px 16px;
  margin-bottom: 16px;

  @media (min-width: $desktop-width) {
    margin-bottom: 24px;
  }
}

.popup__space-item {
  font-family: "Manrope", sans-serif;
  font-size: 14px;
  font-weight: 500;
  line-height: 24px;
  letter-spacing: -0.5px;
}

.popup__features-list {
  display: flex;
  flex-wrap: wrap;
  gap: 4px;
  margin-bottom: 16px;

  @media (min-width: $desktop-width) {
    margin-bottom: 24px;
  }
}

.popup__features-item {
  font-family: "Manrope", sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 16px;
  letter-spacing: -0.5px;
  padding: 4px 12px;
  border-radius: 16px;
  border: 1px solid $border-grey;
}

.popup__link {
  padding: 11px 100px 11px 16px;
  border-radius: 12px 32px 32px 12px;
  background-color: $background-dark-green;
  display: block;
  color: $white;
  font-size: 18px;
  font-weight: 700;
  line-height: 26px;
  letter-spacing: -0.5px;
  position: relative;

  &:hover {
    background-color: $green-hover;
  }

  &::after {
    content: "";
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: 32px;
    width: 65px;
    height: 14px;
    background-image: url("../images/svg/intro-arrow.svg");
    background-repeat: no-repeat;
  }

  @media (min-width: $desktop-width) {
    padding: 16px 180px 16px 16px;
  }
}
