.message {
  background-color: #D9FAEA;
  position: relative;

  &::after {
    content: "";
    height: 100vh;
    width: 100%;
    position: absolute;
    top: 0;
    z-index: -1;
    background-color: #D9FAEA;
  }

  &--error {
    background-color: #FFEBDF;

    &::after {
      background-color: #FFEBDF;
    }
  }

  &--delay {
    background-color: #FFF0D9;

    &::after {
      background-color: #FFF0D9;
    }
  }
}

.message__wrapper {
  padding-top: 96px;

  &--error,
  &--delay {
    padding-bottom: 50px;
  }

  @media (min-width: $desktop-width) {
    padding-top: 208px;
    padding-bottom: 100px;
    min-height: 660px;
    position: relative;
  }
}

.message__title {
  font-size: 32px;
  line-height: 40px;
  letter-spacing: -1px;
  color: $background-dark-green;
  margin-bottom: 12px;

  &--error {
    color: #ff6961;
  }

  &--delay {
    color: #ffb340;
  }

  @media (min-width: $desktop-width) {
    font-size: 56px;
    line-height: 64px;
    letter-spacing: -1px;
    width: 55%;
    margin: 0 auto 12px 0;
  }
}

.message__description {
  font-family: "Manrope", sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 20px;
  letter-spacing: -0.5px;

  &--phone {
    margin-bottom: 50px;
  }

  &--error,
  &--delay {
    margin-bottom: 25px;
  }

  @media (min-width: $desktop-width) {
    font-size: 24px;
    line-height: 130%;
    letter-spacing: -0.3px;
    width: 50%;
    margin-right: auto;
  }
}

.message__link {
  color: inherit;
  border-bottom: 1px solid $grey;

  &:hover {
    color: $green-hover;
    border-color: $green-hover;
  } 
}

.message__img {
  display: block;
  margin: auto 8px 0 auto;

  &--delay,
  &--phone {
    margin: 0 auto;
  }

  &--error {
    margin: auto -16px 0 auto;
  }

  @media (min-width: $desktop-width) {
    position: absolute;
    bottom: 0;
    right: 156px;
    margin: 0;

    &--phone {
      right: 90px;
      bottom: -320px;
    }

    &--error {
      bottom: 50px;
      right: -70px;
    }

    &--delay {
      bottom: 120px;
      right: -150px;
    }
  }
}
