.bill {
  border-radius: 16px;
  padding: 24px 32px;
  background-color: $light-grey;
  position: relative;
}

.bill__title {
  font-size: 24px;
  line-height: 32px;
  letter-spacing: -0.7px;
  margin-bottom: 12px;

  @media (min-width: $desktop-width) {
    margin-bottom: 24px;
  }
}

.bill__img {
  position: absolute;
  top: 68px;
  left: 32px;

  @media (min-width: $desktop-width) {
    top: 80px;
  }
}

.bill__wrapper {
  @media (min-width: $desktop-width) {
    margin-left: 90px;
    margin-bottom: 24px;
  }
}

.bill__second-title {
  font-size: 18px;
  line-height: 26px;
  letter-spacing: -0.5px;
  margin-bottom: 4px;
}

.bill__second-title--margin-mod,
.bill__volume,
.bill__size {
  margin-left: 90px;
  display: block;
  margin-bottom: 4px;

  @media (min-width: $desktop-width) {
    margin-left: 0;
  }
}

.bill__size {
  @media (min-width: $desktop-width) {
    display: inline;
  }
}

.bill__volume {
  @media (min-width: $desktop-width) {
    display: inline;
    margin-right: 15px;
  }
}

.bill__address {
  @media (min-width: $desktop-width) {
    display: block;
  }
}

.bill__container {
  padding-bottom: 8px;
  margin-bottom: 8px;
  border-bottom: 1px solid $border-grey;

  @media (min-width: $desktop-width) {
    padding-bottom: 16px;
    margin-bottom: 16px;
  }
}

.bill__item-wrapper {
  display: flex;
  justify-content: space-between;
  margin-bottom: 4px;
}

.bill__volume,
.bill__size,
.bill__address,
.bill__item-wrapper,
.bill__delivery {
  font-family: "Manrope", sans-serif;
  font-size: 16px;
  font-weight: 500;
  line-height: 24px;
  letter-spacing: -0.5px;
}

.bill__size {
  margin-bottom: 12px;
}

.bill__sum-wrapper {
  display: flex;
  justify-content: space-between;
  font-family: "Manrope", sans-serif;
  font-size: 18px;
  font-weight: 500;
  line-height: 26px;
  letter-spacing: -0.5px;

  @media (min-width: $desktop-width) {
    font-size: 24px;
    line-height: 130%;
    letter-spacing: -0.3px;
  }
}
