.header {
  background-color: rgba($white, 0.9);
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 10000;
  transition: 500ms;
  backdrop-filter: blur(6px);
}

.header__wrapper {
  padding-top: 14px;
  padding-bottom: 24px;
  display: flex;
  justify-content: end;
  align-items: center;
  min-height: 78px;

  @media (min-width: $desktop-width) {
    min-height: 102px;
  }
}

.header__logo-link {
  margin-right: auto;
  cursor: pointer;
}

.header__list {
  display: flex;
  flex-wrap: wrap;
  gap: 32px;
}

.header__item--boxes {
  display: none;

  @media (min-width: $desktop-width) {
    display: block;
  }
}

.header__nav-link {
  font-family: "Manrope", sans-serif;
  color: $black;
  font-size: 14px;
  font-weight: 500;
  line-height: 20px;

  @media (min-width: $desktop-width) {
    font-size: 18px;
    line-height: 26px;
  }

  &:hover {
    border-bottom: 1px solid $black;
  }

  &:disabled {
    opacity: 0.6;
  }
}

.header__button-link {
  padding: 8px 18px;
  color: $white;
  font-size: 16px;
  line-height: 24px;
  background-color: $background-dark-green;
  border-radius: 32px;
  margin-left: 24px;
  flex-shrink: 0;

  &:hover {
    background-color: $green-hover;
  }

  @media (min-width: $desktop-width) {
    font-size: 18px;
    line-height: 26px;
    padding: 19px 64px;
    margin-left: 64px;
  }
}
