.period-intro {
  margin-bottom: 64px;

  @media (min-width: $desktop-width) {
    position: relative;
    margin-bottom: 160px;
  }
}

.period-intro__title {
  font-size: 32px;
  line-height: 40px;
  letter-spacing: -1px;
  margin-bottom: 12px;

  @media (min-width: $desktop-width) {
    font-size: 48px;
    line-height: 56px;
    margin-bottom: 0;
    width: 35%;
    margin-left: auto;
  }
}

.period-intro__wrapper {
  @media (min-width: $desktop-width) {
    width: 35%;
    margin-left: auto;
  }
}

.period-intro__slider {
  margin-bottom: 24px;

  @media (min-width: $desktop-width) {
    position: absolute;
    top: 0;
    left: 64px;
    width: 55%;
  }
}

.period-intro__description-wrapper {
  margin-bottom: 24px;
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.period-intro__description {
  display: block;
  font-family: "Manrope", sans-serif;
  font-size: 14px;
  font-weight: 600;
  line-height: 20px;
  letter-spacing: -0.5px;
  padding-left: 28px;
  background-position: center left 0;
  background-repeat: no-repeat;

  &--access {
    background-image: url("../images/svg/access.svg");
  }

  &--address {
    background-image: url("../images/svg/period-map.svg");
  }
}

.period-intro__second-title {
  font-size: 18px;
  font-weight: 700;
  line-height: 26px;
  letter-spacing: -0.5px;
  margin-bottom: 8px;

  @media (min-width: $desktop-width) {
    font-size: 22px;
    line-height: 30px;
  }
}

.period-form__fieldset {
  padding: 0;
  border: none;
}

.period-intro__list {
  margin-bottom: 24px;
  display: flex;
  gap: 4px;
  flex-wrap: wrap;

  @media (min-width: $desktop-width) {
    gap: 8px;
  }
}

.period-intro__item {
  font-family: "Manrope", sans-serif;
  font-size: 12px;
  font-weight: 400;
  line-height: 16px;
  letter-spacing: -0.5px;
  padding: 4px 12px;
  border-radius: 16px;
  border: 1px solid $border-grey;

  @media (min-width: $desktop-width) {
    font-size: 14px;
    line-height: 20px;
  }
}

.period-form__fieldset {
  margin-bottom: 24px;
}

.period-intro__form {
  margin-bottom: 24px;
}

.period-form__list {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 8px;
  
  @media (min-width: 1440px) {
    grid-template-columns: 1fr 1fr 1fr 1fr;
  }
}

.period-form__label {
  cursor: pointer;
}

.period-intro__link {
  padding: 19px 16px;
  border-radius: 12px 32px 32px 12px;
  background-color: $background-dark-green;
  display: block;
  color: $white;
  font-size: 18px;
  line-height: 26px;
  letter-spacing: -0.5px;
  position: relative;

  &:hover {
    background-color: $green-hover;
  }

  &::after {
    content: "";
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: 32px;
    width: 65px;
    height: 14px;
    background-image: url("../images/svg/intro-arrow.svg");
    background-repeat: no-repeat;
  }

  @media (min-width: $desktop-width) {
    border-radius: 24px 102px 102px 24px;
    font-size: 32px;
    font-weight: 700;
    line-height: 40px;
    letter-spacing: -1px;
    padding: 20px 180px 20px 32px;
    margin: 0;

    &::after {
      background-image: url("../images/svg/intro-arrow-desktop.svg");
      width: 111px;
      height: 39px;
    }
  }
}
