.slider__main {
  max-width: 288px;
  position: relative;
  margin: 0 auto;
  overflow: hidden;
	margin-bottom: 16px;

  @media (min-width: $desktop-width) {
    max-width: 900px;
  }
}

.slider__thumbnails {
  max-width: 288px;
  position: relative;
  margin: 0 auto;

  @media (min-width: $desktop-width) {
    margin: 0 auto 0 0;
  }
}

.slider__img {
  cursor: pointer;
}

.slider__big-img {
  cursor: pointer;

  @media (min-width: $desktop-width) {
    width: 100%;
    height: auto;
  }
}

.swiper-slide-thumb-active .slider__img {
  outline: 2px solid $green-hover;
	border-radius: 12px;
}

.swiper-button-next,
.swiper-button-prev {
  display: none !important;
  --swiper-navigation-size: 25px;
  --swiper-navigation-color : #636366;

  @media (min-width: $desktop-width) {
    display: block !important;
  }
}