.result__title {
  font-size: 24px;
  line-height: 32px;
  letter-spacing: -0.7px;
  margin-bottom: 12px;

  @media (min-width: $desktop-width) {
    font-size: 32px;
    line-height: 40px;
    letter-spacing: -1px;
  }
}

.result__list {
  display: flex;
  flex-direction: column;
  gap: 12px;
  margin-bottom: 12px;

  &:last-child {
    margin-bottom: 0;
  }

  @media (min-width: $desktop-width) {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 8px;
    margin-bottom: 40px;

    &--big {
      grid-template-columns: 1fr 1fr;
    }
  }
}

.result__link {
  display: block;
  background-color: $light-grey;
  border-radius: 24px;
  padding: 24px 12px 12px 12px;
  color: inherit;

  @media (min-width: $desktop-width) {
    min-height: 624px;
    padding: 24px;
    display: flex;
    flex-direction: column;
    justify-content: end;
  }
}

.result__wrap {
  @media (min-width: $desktop-width) {
    height: 330px;
  }
}

.result__img {
  display: block;
  margin: 0 auto 16px auto;

  @media (min-width: $desktop-width) {
    margin-bottom: 122px;
    
    &--15 {
      margin-bottom: 75px;
    }
    &--2 {
      margin-bottom: 72px;
    }
    &--4 {
      margin-bottom: 52px;
    }
    &--8 {
      margin-bottom: 18px;
    }
  }
}

.result__wrapper {
  border-radius: 12px;
  padding: 16px;
  display: flex;
  flex-direction: column;
  gap: 8px;
  background-color: $white;

  @media (min-width: $desktop-width) {
    padding: 24px;
    gap: 12px;
  }
}

.result__item-title {
  font-size: 18px;
  line-height: 26px;
  letter-spacing: -0.5px;

  @media (min-width: $desktop-width) {
    font-size: 24px;
    line-height: 32px;
    letter-spacing: -0.7px;
  }
}

.result__square-list {
  display: flex;
  gap: 12px 8px;
  flex-wrap: wrap;
}

.result__square-item {
  font-family: "Manrope", sans-serif;
  font-size: 12px;
  font-weight: 400;
  line-height: 16px;
  letter-spacing: -0.5px;

  @media (min-width: $desktop-width) {
    font-size: 14px;
    line-height: 20px;
  }
}

.result__for-list {
  display: flex;
  gap: 4px;
  flex-wrap: wrap;
}

.result__for-item {
  border-radius: 16px;
  padding: 4px 12px;
  border: 1px solid $border-grey;
  font-family: "Manrope", sans-serif;
  font-size: 12px;
  font-weight: 400;
  line-height: 16px;
  letter-spacing: -0.5px;

  @media (min-width: $desktop-width) {
    font-size: 14px;
    font-weight: 500;
    line-height: 20px;
  }
}
