.contacts {
  padding-top: 80px;
  margin-bottom: 64px;

  @media (min-width: $desktop-width) {
    padding-top: 176px;
    margin-bottom: 80px;
  }
}

.contacts__title {
  font-size: 32px;
  line-height: 40px;
  letter-spacing: -1px;
  margin-bottom: 24px;

  @media (min-width: $desktop-width) {
    font-size: 56px;
    line-height: 64px;
    letter-spacing: -1px;
    margin-bottom: 40px;
  }
}

.contacts__second-title {
  font-size: 24px;
  line-height: 32px;
  letter-spacing: -0.7px;
  margin-bottom: 12px;

  @media (min-width: $desktop-width) {
    font-size: 32px;
    line-height: 40px;
    letter-spacing: -1px;
    margin-bottom: 24px
  }
}

.contacts__list {
  border-radius: 16px;
  padding: 16px;
  background-color: $light-grey;
  display: flex;
  flex-direction: column;
  gap: 16px;
  margin-bottom: 24px;

  @media (min-width: $desktop-width) {
    border-radius: 24px;
    padding: 40px;
    display: grid;
    grid-template-columns: auto auto;
  }

  @media (min-width: 1400px) {
    display: flex;
    flex-direction: row;
    gap: 70px;
    flex-wrap: wrap;
  }
}

.contacts__description {
  display: block;
  font-family: "Manrope", sans-serif;
  font-size: 16px;
  font-weight: 500;
  line-height: 24px;
  letter-spacing: -0.5px;

  @media (min-width: $desktop-width) {
    font-size: 18px;
    line-height: 26px;
    letter-spacing: normal;
  }
}

.contacts__social-link {
  font-size: 18px;
  line-height: 26px;
  letter-spacing: -0.5px;
  color: $black;

  &:hover {
    border-bottom: 1px solid $black;
  }

  @media (min-width: $desktop-width) {
    font-size: 24px;
    line-height: 32px;
    letter-spacing: -0.7px;
  }
}

.contacts__social-list {
  display: flex;
  flex-wrap: wrap;
  gap: 12px;
}

.contacts__social-item {
  position: relative;

  ::before {
    position: absolute;
    content: "";
    top: 50%;
    transform: translateY(-50%);
    left: -8px;
    width: 5px;
    height: 5px;
    background-color: $black;
    border-radius: 50%;
  }
}

.contacts__social-item:nth-child(1) {
  ::before {
    display: none;
  }
}
