.fieldset {
  border: none;
  padding: 0;
  margin-bottom: 16px;
  position: relative;

  @media (min-width: $desktop-width) {
    margin-bottom: 0;
  }
}

.fieldset__title {
  padding: 0 22px 0 0;
  font-family: "Manrope", sans-serif;
  font-size: 18px;
  font-weight: 500;
  line-height: 26px;
  cursor: pointer;
  background-image: url("../images/svg/filters-arrow.svg");
  background-position: center right 0;
  background-repeat: no-repeat;
}

.fieldset--open {
  color: $green-hover;
  background-image: url("../images/svg/filters-arrow-open.svg");
}

.fieldset__list {
  display: none;
}

.fieldset--open + .fieldset__list {
  padding-top: 10px;
  display: flex;
  flex-direction: column;
  gap: 16px;
  
  @media (min-width: $desktop-width) {
    position: absolute;
    top: 100%;
    left: 0;
    padding: 20px 24px;
    border-radius: 12px;
    background-color: $white;
    width: max-content;
    box-shadow: 0px 2.76726px 2px 0px rgba(0, 0, 0, 0.02), 0px 6.6501px 5.32008px 0px rgba(0, 0, 0, 0.03), 0px 12.52155px 10.01724px 0px rgba(0, 0, 0, 0.04);

    &--items {
      display: grid;
      grid-template-columns: auto auto auto auto;
      gap: 16px 24px;
    }
  }
}

.fieldset__item {
  font-family: "Manrope", Arial, sans-serif;
  font-size: 16px;
  font-weight: 500;
  line-height: 24px;
  letter-spacing: -0.5px;
}
