.boxes__title {
  font-size: 32px;
  line-height: 40px;
  letter-spacing: -1px;
  margin-bottom: 12px;

  @media (min-width: $desktop-width) {
    font-size: 56px;
    line-height: 64px;
    margin-bottom: 16px;
  }
}

.boxes__form {
  margin-bottom: 24px;

  @media (min-width: $desktop-width) {
    margin-bottom: 40px;
  }
}

.boxes__result {
  margin-bottom: 64px;
}
