.breadcrumbs {
  padding-top: 78px;
  margin-bottom: 12px;

  @media (min-width: $desktop-width) {
    padding-top: 182px;
  }
}

.breadcrumbs__items {
  flex-shrink: 0;
  padding-right: 23px;
  background-image: url("../images/svg/breadcrumbs-arrow.svg");
  background-position: right 4px center;
  background-repeat: no-repeat;

  &:last-child {
    background-image: none;
  }
}

.breadcrumbs__link {
  font-family: "Manrope", Arial, sans-serif;
  font-size: 14px;
  font-weight: 500;
  line-height: 20px;
  letter-spacing: -0.5px;
  color: inherit;

  @media (min-width: $desktop-width) {
    font-size: 16px;
    line-height: 24px;
  }
}

.breadcrumbs__link--active {
  color: $background-dark-green;
}

.breadcrumbs__list {
  display: flex;
  flex-wrap: wrap;

  @media (min-width: $desktop-width) {
    margin-bottom: 16px;
  }
}
