.warehouse {
  padding-bottom: 64px;

  @media (min-width: $desktop-width) {
    padding-bottom: 80px;
  }
}

.warehouse__title {
  font-size: 32px;
  line-height: 40px;
  letter-spacing: -1px;

  @media (min-width: $desktop-width) {
    font-size: 56px;
    line-height: 64px;
    margin-bottom: 8px;
  }
}

.warehouse__discription {
  font-family: "Manrope", sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 20px;
  letter-spacing: -0.5px;
  margin-bottom: 12px;

  @media (min-width: $desktop-width) {
    font-size: 18px;
    line-height: 26px;
    margin-bottom: 0;
  }
}

.warehouse__wrapper {
  margin-bottom: 24px;

  @media (min-width: $desktop-width) {
    display: flex;
    justify-content: space-between;
    margin-bottom: 40px;
  }
}

.warehouse__link-wrapper {
  display: flex;
  gap: 24px;
}

.warehouse__link {
  font-family: "Manrope", sans-serif;
  font-size: 16px;
  font-style: normal;
  font-weight: 500;
  line-height: 24px;
  letter-spacing: -0.5px;
  padding: 0;
  color: $grey;
  display: flex;
  gap: 8px;
  align-items: last baseline;

  &--active {
    color: $black;
  }

  @media (min-width: $desktop-width) {
    font-size: 18px;
    line-height: 26px;
  }
}

.warehouse__list {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.warehouse__container {
  position: relative;
}

.warehouse__map {
  display: block;
  width: 100%;
  height: 298px;
  background-color: #dddddd;
  border-radius: 24px;

  @media (min-width: $desktop-width) {
    height: 622px;
  }
}
