*,
*::before,
*::after {
  box-sizing: border-box;
}

sup {
  font-size: 10px;
}

html {
  height: 100%;
}

body {
  margin: 0;
  padding: 0;
  overflow-x: hidden;

  font-family: "GraphikLCG", "Manrope", sans-serif;
  font-size: 16px;
  line-height: 18px;
  font-weight: 700;
  color: $black;
  height: 100%;
  position: relative;
}

a {
  text-decoration: none;
}

h1, h2, h3 {
  margin: 0;
}

p {
  margin: 0;
  padding: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}
