.checkbox {
  display: flex;
  align-items: center;
  gap: 8px;
  cursor: pointer;
  width: fit-content;
}

.checkbox__description {
  order: 2;
  flex-shrink: 0;
}

.checkbox__mark {
  order: 1;
  width: 20px;
  height: 20px;
  border-radius: 4px;
  border: 2px solid $black;
  flex-shrink: 0;
}

.checkbox__input:checked + .checkbox__mark {
  border: none;
  background-image: url("../images/svg/filters-tick.svg");
  background-repeat: no-repeat;
  background-position: center;
}

.checkbox__input:focus-visible + .checkbox__mark {
  outline: 2px solid $black;
}

