.period-radio__wrapper {
  padding: 8px 12px;
  border-radius: 8px;
  background-color: $light-grey;
  display: block;
}

.period-radio__input:checked + .period-radio__wrapper {
  background-color: $background-dark-green;
  color: $white;
}

.period-radio__time {
  display: block;
  font-family: "Manrope", sans-serif;
  font-size: 16px;
  font-weight: 500;
  line-height: 24px;
  letter-spacing: -0.5px;
  text-align: center;
}

.period-radio__price {
  display: block;
  font-family: "Manrope", sans-serif;
  font-size: 12px;
  font-weight: 400;
  line-height: 16px;
  letter-spacing: -0.5px;
  text-align: center;
}
