.radio {
  display: flex;
  align-items: center;
  gap: 8px;
  cursor: pointer;
  width: fit-content;
}

.radio__description {
  order: 2;
  flex-shrink: 0;
}

.radio__mark {
  order: 1;
  width: 14px;
  height: 14px;
  border-radius: 50%;
  outline: 2px solid $black;
  flex-shrink: 0;
}

.radio__input:checked + .radio__mark {
  outline-color: $green-hover;
  position: relative;

  &::after {
    content: "";
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translateX(-50%) translateY(-50%);
    width: 8px;
    height: 8px;
    border-radius: 50%;
    background-color: $green-hover;
  }
}

.radio__input:focus-visible + .radio__mark {
  outline: 2px solid $black;
}

.radio__schedule,
.radio__round-the-clock {
  display: block;
  font-family: "Manrope", sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 12px;
  letter-spacing: -0.5px;
  padding-left: 28px;
  color: $green-hover;
}

.radio__schedule {
  color: $background-purple;
}
