.payment-container {
  @media (min-width: $desktop-width) {
    max-width: 942px;
    margin: 0 auto;
  }
}

.payment {
  margin-bottom: 64px;

  @media (min-width: $desktop-width) {
    margin-bottom: 80px;
  }
}

.payment__title {
  font-size: 32px;
  line-height: 40px;
  letter-spacing: -1px;
  margin-bottom: 24px;

  @media (min-width: $desktop-width) {
    font-size: 48px;
    line-height: 56px;
    margin-bottom: 40px;
  }
}

.payment__wrapper {
  @media (min-width: $desktop-width) {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 40px;
    margin-bottom: 40px;
  }
}

.payment__description {
  font-family: "Manrope", sans-serif;
  font-size: 16px;
  font-weight: 500;
  line-height: 24px;
  letter-spacing: -0.5px;
  border-radius: 8px;
  padding: 16px;
  background-color: #fff1af;
  margin-bottom: 24px;
}

.payment__timer {
  border-radius: 8px;
  padding: 0 12px;
  border: 1px solid $black;
  margin: 0 3px;
}

.payment__second-title {
  font-size: 24px;
  line-height: 32px;
  letter-spacing: -0.7px;
  margin-bottom: 12px;
}

.payment__input {
  font-family: "Manrope", sans-serif;
  font-size: 16px;
  font-weight: 500;
  line-height: 24px;
  letter-spacing: -0.5px;
  padding: 20px 24px;
  border-radius: 8px;
  border: 1px solid $border-grey;
  display: block;
}

.payment__form {
  display: flex;
  flex-direction: column;
  gap: 8px;
  margin-bottom: 24px;

  @media (min-width: $desktop-width) {
    margin-bottom: 0;
  }
}

.payment__bill {
  margin-bottom: 24px;

  @media (min-width: $desktop-width) {
    margin-bottom: 0;
  }
}

.payment__link {
  padding: 19px 16px;
  border-radius: 12px 32px 32px 12px;
  background-color: $background-dark-green;
  display: block;
  color: $white;
  font-size: 18px;
  line-height: 26px;
  letter-spacing: -0.5px;
  position: relative;

  &:hover {
    background-color: $green-hover;
  }

  &::after {
    content: "";
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: 32px;
    width: 65px;
    height: 14px;
    background-image: url("../images/svg/intro-arrow.svg");
    background-repeat: no-repeat;
  }

  @media (min-width: $desktop-width) {
    border-radius: 24px 102px 102px 24px;
    font-size: 32px;
    font-weight: 700;
    line-height: 40px;
    letter-spacing: -1px;
    padding: 20px 180px 20px 32px;
    margin: 0;
    width: 442px;

    &::after {
      background-image: url("../images/svg/intro-arrow-desktop.svg");
      width: 111px;
      height: 39px;
    }
  }
}
