.response {
  background-color: rgba(#ffbab7, 0.6);
  background-image: url("../images/content/404.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: 274px;
  height: 100vh;
  display: flex;

  &--500 {
    background-image: url("../images/content/500.png");
  }

  @media (min-width: $desktop-width) {
    background-image: url("../images/content/404-desktop.png");
    background-size: 513px;

    &--500 {
      background-image: url("../images/content/500-desktop.png");
      background-size: 713px;
    }
  }
}

.response__wrapper {
  padding: 0 24px;
  margin: 45vh auto auto auto;

  &--500 {
    margin: 50vh auto auto auto;
  }

  @media (min-width: $desktop-width) {
    margin: 55vh auto auto auto;

    &--500 {
      margin: 50vh auto auto auto;
    }
  }
}

.response__description {
  font-family: "Manrope", sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 20px;
  letter-spacing: -0.3px;
  text-align: center;
  margin-bottom: 24px;

  @media (min-width: $desktop-width) {
    font-size: 24px;
    line-height: 130%;
    width: 612px;
    margin: 0 auto 40px auto;
  }
}

.response__description-link {
  color: inherit;

  &:hover {
    border-bottom: 1px solid $grey;
  }
}

.response__link {
  padding: 8px 18px;
  color: $white;
  font-size: 16px;
  line-height: 24px;
  background-color: $background-dark-green;
  border-radius: 32px;
  margin: 0 auto;
  display: block;
  width: fit-content;

  &:hover {
    background-color: $green-hover;
  }

  @media (min-width: $desktop-width) {
    font-size: 18px;
    line-height: 26px;
    padding: 19px 64px;
  }
}
