.period-checkbox {
  display: flex;
  gap: 8px;
  cursor: pointer;
  width: fit-content;
}

.period-checkbox__mark {
  display: block;
  width: 18px;
  height: 18px;
  border-radius: 4px;
  border: 2px solid $green-hover;
  flex-shrink: 0;
}

.period-checkbox__input:checked + .period-checkbox__mark {
  // width: 20px;
  // height: 20px;
  border: none;
  background-image: url("../images/svg/period-tick.svg");
  background-repeat: no-repeat;
  background-position: center;
}

.period-checkbox__description {
  font-family: "Manrope", sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 20px;
  letter-spacing: -0.5px;
}
